import creepConfigs from '../config.creep.js'
import {decayTime} from "../global";

Creep.prototype.work = function() {
    // Ê£ÄÊü• creep ÂÜÖÂ≠ò‰∏≠ÁöÑËßíËâ≤ÊòØÂê¶Â≠òÂú®
    if (!(this.memory.role in creepConfigs)) {
        console.log(`creep ${this.name} ÂÜÖÂ≠òÂ±ûÊÄß role ‰∏çÂ±û‰∫é‰ªª‰ΩïÂ∑≤Â≠òÂú®ÁöÑ creepConfigs ÂêçÁß∞`)
        return
    }
    // Ëé∑ÂèñÂØπÂ∫îÈÖçÁΩÆÈ°π
    const creepConfig = creepConfigs[this.memory.role]

    // Ëé∑ÂèñÊòØÂê¶Â∑•‰Ωú
    const working = creepConfig.switch ? creepConfig.switch(this) : true

    // ÊâßË°åÂØπÂ∫îÊìç‰Ωú
    if (working) {
        if (creepConfig.target&&creepConfig.otherRoom){
            this.avoid(creepConfig.otherRoom,creepConfig.target)
        }
        else {creepConfig.target(this)}
    }
    else {
        if (creepConfig.source&&creepConfig.otherRoom){
            this.avoid(creepConfig.otherRoom,creepConfig.source)
        }else{
            creepConfig.source(this)
        }
    }
}

Creep.prototype.avoid = function(roomString,fn){
    const room = Game.rooms[roomString]
    if (!Memory.invader) {
        Memory.invader = {};
        Memory.invader[roomString] = 0
    }
    if (room && room.find(FIND_HOSTILE_CREEPS).length) { // ÈÅáÂà∞invaderËÆ°Êó∂ÔºåÂæÄÂá∫ÁîüÁÇπË∑ë
        if (Memory.invader[roomString] + decayTime < Game.time) { //‰∏çÂú®‰æµÁï•Êó∂Èó¥ÊÆµÔºåËÆ∞ÂΩïÂºÄÂßãÊó∂Èó¥
            Memory.invader[roomString] = Game.time;
        }
        this.moveTo(new RoomPosition(9, 2, 'E2S35'))
    } else if (Memory.invader[roomString] + decayTime > Game.time) { //Âú®‰æµÁï•Êó∂Èó¥ÊÆµ
        this.moveTo(new RoomPosition(9, 2, 'E2S35'))
    }
    else if (!room && Memory.invader[roomString] + decayTime < Game.time) { //Ê≤°ËßÜÈáéÔºå‰∏çË¢´‰æµÁï•
        this.moveTo(new RoomPosition(20, 36, roomString))
    } else if (Memory.invader[roomString] + decayTime < Game.time) { //ÂΩìÂâç‰∏çÂú®‰æµÁï•Êó∂Èó¥ÊÆµ
        fn(this)
    }

}

Creep.prototype.updateState = function()
{
    // creep Ë∫´‰∏äÊ≤°ÊúâËÉΩÈáè && creep ‰πãÂâçÁöÑÁä∂ÊÄÅ‰∏∫‚ÄúÂ∑•‰Ωú‚Äù
    if(this.store[RESOURCE_ENERGY] <= 0 && this.memory.working) {
        this.memory.working = false
    }
    // creep Ë∫´‰∏äËÉΩÈáèÊª°‰∫Ü && creep ‰πãÂâçÁöÑÁä∂ÊÄÅ‰∏∫‚Äú‰∏çÂ∑•‰Ωú‚Äù
    if(this.store[RESOURCE_ENERGY] >= this.store.getCapacity() && !this.memory.working) {
        this.memory.working = true
    }

    return this.memory.working
}
